*{
    margin: 0;
    padding: 0;
}
@keyframes intoView{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
h1{
    text-align: center;
    color: white;
    margin: 2rem;
    font-family: 'Press Start 2P', cursive;
}
body{
    padding-top: 2rem;
    background: #03012c;
}
.movies-container{
    display: flex;
    
    flex-wrap: wrap;

}
.movies-container p{
    animation: intoView 2s;
    color: white;
}

.single-movie{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Century Gothic, sans-serif;
    background: #190e4f;
    max-width: 200px;
    margin: 2rem;
    position: relative;
    border-radius: 15px;
    transition: .5s ease;
    min-height: 600px;
    min-width: 300px;
}
.single-movie:hover{
    transform:scale(1.1);
    z-index: 3;
    box-shadow: 0 0 10px red;
}

.single-movie p{
    margin: 1rem;
}
.moviePreview{
    width: 100%;
    margin: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x: hidden;


}
.moviePreview marquee{
    width: 80%;
}
.moviePreview h2{
    min-height: 4rem;
    white-space:nowrap;
    align-self: flex-start;
    margin-left: .5rem;
    color: white;
    
}
.details-container{
    position: absolute;
    bottom: 0;
    backdrop-filter: blur(20px);
    border-radius: 15px;
    
}

img{
    height: 300px;
    max-width: 200px;
    
}
@media only screen and (max-width: 600px){
    .single-movie{
        width: 80%;
        
    }
}
